<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="transmit-page create-action-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-show="onShow"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="智能维修">
        <i class="icon-wode nav-icon-right" slot="right" @click="handleToMe"></i>
      </van-nav-bar>
    </div>

    <van-loading type="spinner" v-if="loading" class="page-loading" />

    <transition-group class="transmit-list" tag="div" name="list">
      <item 
        @click.native="handleDetail(item)"
        v-for="(item) in list"
        :key="item.id"
        :data="item"
      >
      </item>
    </transition-group>

    <template slot="blank">
      <div class="trouble-action" @click="handleCreateTrouble"></div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import Item from './Item'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import moment from 'moment'

export default {
  components: {
    Page,
    Item,
    // AScroll
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    header: null,
    list: [],
    filter: {
      pageIndex: 1,
      pageSize: 10,
    },
    loading: false
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      console.log(JSON.stringify(header))
      console.log(JSON.stringify(main))

      this.headerHeightVW = (this.header.h + 0) * 100 / this.header.w
      this.onRefresh()
    },

    onShow () {
      if (localStorage.getItem('refresh_trouble_id')) {
        io.getTroubleListId({id: localStorage.getItem('refresh_trouble_id')}).then(data => {
          
          const index = this.list.findIndex(item => item.id == data.detail.id)

          if (index >= 0) {
            this.$set(this.list, index, data.detail)
          }

          localStorage.setItem('refresh_trouble_id', '')
        })
      }
    },

    onRefresh () {
      this.filter.pageIndex = 1

      io.getTroubleList(this.filter).then(data => {
        this.list = data.list
        this.filter.pageIndex++
        this.$refs.page.finishRefresh()
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.finishRefresh()
        this.loading = false
      })
    },

    onLoad () {
      io.getTroubleList(this.filter).then(data => {
        this.list = this.list.concat(data.list)
        this.filter.pageIndex++
        if (data.list.length == 0) {
          this.$refs.page.endLoad()
        } else {
          this.$refs.page.updateLoad()
        }
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.updateLoad()
        this.loading = false
      })
    },

    handleDetail (item) {
      localStorage.setItem('refresh_trouble_id', item.id)
      navigate.open('troubleDetail', {id: item.id, status: item.status})
    },

    handleCreateTrouble () {
      navigate.open('createTrouble')
    },

    handleToMe () {
      navigate.open('me')
    }

  },
  watch: {
    'filter.status': function (val) {
      this.onRefresh()
    },
    'filter.source': function (val) {
      this.onRefresh()
    }
  }
}
</script>

